import React from 'react';
import { bool, func } from 'prop-types';

import {
  Text,
  Box,
  Center,
  Button,
  Drawer,
  DrawerOverlay,
  DrawerContent,
  DrawerBody,
  DrawerHeader,
  DrawerCloseButton,
} from '@chakra-ui/react';

import txtWording from './locales';
import { string } from 'prop-types';

function ModalAddress({ onClose, visible, onCopyText, lang }) {
  const address = `Graha Estetika, Jl. Serasi B1-B2, RT/RW 002/0001, Pandaran, Banyumanik, Semarang`;
  const receiverName = `Nailal Via`;

  const renderBank = () => {
    return (
      <Box padding="16px 0">
        <Text textAlign="center" marginTop="0" fontSize="lg">
          {receiverName} <br />
        </Text>
        <Text textAlign="center" color="black" fontSize="sm">
          {address}
        </Text>
        <Center>
          <Button
            size="xs"
            fontWeight="normal"
            colorScheme="blackAlpha"
            bgColor="mainColor"
            marginTop="16px"
            textColor="mainColorTextLight"
            onClick={() => onCopyText(`${receiverName} - ${address}`, txtWording.successCopyAddress[lang])}
          >
            {txtWording.modalCopy[lang]}
          </Button>
        </Center>
      </Box>
    );
  };

  return (
    <Drawer placement="bottom" onClose={onClose} isOpen={visible}>
      <DrawerOverlay />
      <DrawerContent>
        <DrawerCloseButton marginTop="12px" />
        <DrawerHeader
          fontFamily="fantasy"
          borderBottomWidth="1px"
          fontSize="2xl"
          fontWeight="normal"
          color="mainColorText"
        >
          {txtWording.sendGift[lang]}
        </DrawerHeader>
        <DrawerBody>
          <Text align="center" fontSize="sm" margin="16px 0" color="black">
            {txtWording.modalDescAddress[lang]}
          </Text>
          {renderBank()}
        </DrawerBody>
      </DrawerContent>
    </Drawer>
  );
}

ModalAddress.propTypes = {
  lang: string,
  visible: bool.isRequired,
  onClose: func.isRequired,
  onCopyText: func.isRequired,
};

ModalAddress.defaultProps = {
  lang: 'id',
};

export default ModalAddress;
